body {
  /* Hacker News fonts & background color */
  font-family: Verdana, Geneva, sans-serif;
  background-color: rgb(246, 246, 239);
}

.App {
  text-align: center;
}

.App-header {
  /* Hacker News orange */
  background-color: rgb(255, 102, 0);
  height: 30px;
  padding: 20px;
  color: #000000;
  margin-bottom: 20px;
}

.App-title {
  font-size: 1.5em;
  margin: 0;
}

.App-intro {
  font-size: large;
}

.story {
  margin-bottom: 20px;
  padding-left: 10px;
  padding-right: 10px;
}

/* Hacker News styling */

.secondary-text {
  
  color: #828282;
  font-size: small;
}

a:visited {
  color: #828282;
  text-decoration: none;
}

a:link {
  color: #000000;
  text-decoration: none;
}

ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

/* Pure CSS Loader from loading.io */

.lds-dual-ring {
  display: inline-block;
  width: 64px;
  height: 64px;
}
.lds-dual-ring:after {
  content: " ";
  display: block;
  width: 46px;
  height: 46px;
  margin: 1px;
  border-radius: 50%;
  border: 5px solid rgb(255, 102, 0);
  border-color: rgb(255, 102, 0) transparent rgb(255, 102, 0) transparent;
  -webkit-animation: lds-dual-ring 1.2s linear infinite;
          animation: lds-dual-ring 1.2s linear infinite;
}
@-webkit-keyframes lds-dual-ring {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
@keyframes lds-dual-ring {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}



